<template>
  <div class="referral-history-page">
    <div class="referral-history-page-wrap">
    <div class="page-header">
      <h1>Referral Actions History</h1>
      <p>Detailed history of actions performed by referral: <span class="referral-tag">{{ referralTag }}</span></p>
      
      <div class="back-link">
        <router-link to="/my/referrals" class="back-button">
          <i class="fas fa-arrow-left"></i> Back to Referrals
        </router-link>
      </div>
    </div>
    
    <!-- Статистика истории -->
    <div class="history-stats">
      <div class="stat-card">
        <h3>Total Actions</h3>
        <div class="stat-value">{{ history.length }}</div>
      </div>
      <div class="stat-card">
        <h3>Total Rewards</h3>
        <div class="stat-value">{{ totalRewards.toFixed(6) }} SOL</div>
      </div>
    </div>
    
    <!-- Список действий -->
    <div class="history-list">
      <h2>Actions History</h2>
      
      <div v-if="loading" class="loading">
        <p v-if="isAuthenticated">Loading history...</p>
        <p v-else>Please <router-link to="/my">Login</router-link> to view referral history</p>
      </div>
      
      <div v-else-if="history.length === 0" class="no-history">
        <p v-if="isAuthenticated">No actions have been recorded for this referral yet.</p>
        <p v-else>Please <router-link to="/my">Login</router-link> to view referral history</p>
      </div>
      
      <div v-else class="history-items">
        <div v-for="item in history" :key="item.id" class="history-item">
          <div class="history-info">
            <div class="history-action">{{ formatAction(item.action) }}</div>
            <!-- <div class="history-description">{{ item.description }}</div> -->
            <div class="history-date">{{ formatDate(item.createdAt) }}</div>
            <div class="history-meta" v-if="item.meta">
              <span class="meta-label">Related to:</span> {{ item.meta }}
            </div>
          </div>
          <div class="history-amounts">
            <div class="amount">
              <span class="amount-label">Amount:</span>
              <span class="amount-value">{{ (item.amount / 1e9).toFixed(6) }} SOL</span>
            </div>
            <div class="reward">
              <span class="reward-label">Reward ({{ item.percent }}%):</span>
              <span class="reward-value">{{ (item.reward / 1e9).toFixed(6) }} SOL</span>
            </div>
            <div class="transaction" v-if="item.transaction">
              <a :href="`https://solscan.io/tx/${item.transaction}`" target="_blank" class="transaction-link">
                View Transaction <i class="fas fa-external-link-alt"></i>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Уведомление -->
    <div v-if="toast.show" class="toast" :class="toast.type">
      {{ toast.message }}
    </div>
  </div>
  </div>
</template>

<script>
import { ref, onMounted, computed } from 'vue';
import { useRoute } from 'vue-router';
import store from '@/vuex';
import axios from 'axios';

export default {
  name: 'ReferralHistoryPage',
  
  setup() {
    const route = useRoute();
    const referralTag = ref('');
    const history = ref([]);
    const loading = ref(true);
    const toast = ref({
      show: false,
      message: '',
      type: 'success'
    });
    
    // Получаем referralTag из URL
    referralTag.value = route.params.referralTag;
    
    // Проверка аутентификации
    const isAuthenticated = computed(() => {
      return store.getters.isAuthenticated;
    });
    
    // Вычисляем общую сумму вознаграждений
    const totalRewards = computed(() => {
      return history.value.reduce((sum, item) => sum + (item.reward / 1e9), 0);
    });
    
    // Загрузка истории реферала
    const loadHistory = async () => {
      if (!isAuthenticated.value) {
        loading.value = false;
        return;
      }
      
      try {
        loading.value = true;
        const response = await axios.get(`/referrals/${referralTag.value}/history`);
        
        if (response.data.success) {
          history.value = response.data.history || [];
        } else {
          showToast(response.data.message || 'Failed to load referral history', 'error');
        }
      } catch (error) {
        console.error('Error loading referral history:', error);
        showToast(error.response?.data?.message || 'Failed to load referral history', 'error');
      } finally {
        loading.value = false;
      }
    };
    
    // Форматирование даты
    const formatDate = (dateString) => {
      if (!dateString) return 'N/A';
      
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    };
    
    // Форматирование типа действия
    const formatAction = (action) => {
      if (!action) return 'Unknown Action';
      
      // Преобразуем snake_case в Title Case с пробелами
      return action
        .split('_')
        .map(word => word.charAt(0).toUpperCase() + word.slice(1))
        .join(' ');
    };
    
    // Показать уведомление
    const showToast = (message, type = 'success') => {
      toast.value = {
        show: true,
        message,
        type
      };
      
      // Скрыть уведомление через 3 секунды
      setTimeout(() => {
        toast.value.show = false;
      }, 3000);
    };
    
    // Загружаем данные при монтировании компонента
    onMounted(() => {
      loadHistory();
    });
    
    return {
      referralTag,
      history,
      loading,
      toast,
      isAuthenticated,
      totalRewards,
      formatDate,
      formatAction
    };
  }
};
</script>

